.SignupModal{
    position: fixed;
    height: 100%;
    z-index: 400;
    width: 80%;
    left: 10%;
    right: 10%;
    background-color: white;
    border: none;
    padding: 6%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}

.LoginHeader {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    align-items: center;
    margin-bottom: 10%;
  }
  
  .LoginHeader img {
    height: 20%;
    width: 20%;
    text-align: center;
    margin-bottom: 2%;
  }
  
  .LoginHeader p {
      font-size: 16px;
      font-weight: bold;
      font-family: monospace;
      color: rgba(0, 0, 0, 0.911);
      text-align: center;
  }

.Open {
    transform: translateY(0);
    opacity: 1;
}

.Close {
    transform: translateY(-100vh);
    opacity: 0;
}

.Form {
    margin-top: 0;
}

.SignupButton{
    height: 45px;
   
}
.SignupButtonDiv{
    margin-top: 8%;
}

.GoogleIcon {
    text-align: center;
    margin-top: 10%;
    cursor: pointer;
  }
  
  .GoogleIcon img {
    height: 20%;
    width: 20%;
  }

@media (min-width: 580px) {
    .SignupModal {
        width: 550px;
        padding: 5%;
        /* claculating the middle position */
        left: calc(50% - 275px);
    }
}